.yoga-section-header {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    &::before {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        z-index: 0;
        background-color: #0a0004;
        opacity: 0.5;
    }
}
.yoga-section-2 {
    .card-service {
        transition: all 0.2s ease-in-out;
        &:hover {
            border-color: var(--tc-theme-primary) !important;
        }
    }
}
.text-section-about {
    margin-bottom: -110px;
}
.yoga-section-3 {
    .carouselTicker__item {
        margin: 0 20px;
    }
}
.yoga-home-section-4,
.yoga-about-section-5,
.yoga-blog-section-1 {
    // Swiper customize
    .swiper-button-next,
    .swiper-button-prev {
        --swiper-navigation-size: 16px;
        --swiper-navigation-top-offset: 50%;
        --swiper-navigation-sides-offset: 10px;
        --swiper-navigation-color: var(--tc-system-black);
        display: flex;
        width: 50px;
        height: 50px;
        padding: 0px;
        font-size: 16px;
        border-radius: 50px;
        background-color: var(--tc-system-white);
        border: 1px solid var(--tc-system-navy-blue-3);
        svg {
            width: 16px;
            height: 16px;
        }
        &:hover,
        :active {
            background-color: var(--tc-system-black);
            svg {
                path {
                    fill: var(--tc-system-white);
                }
            }
        }
    }
}
.yoga-section-5 {
    .select-form {
        select {
            color: #666;
            background-color: rgba(255, 255, 255, 0.1);
            height: 60px;
            border-radius: 0;
            padding-left: 3rem;
            border: 1px solid rgba(255, 255, 255, 0.25);
            background-image: url(../imgs/template/icons/arrow-down.svg);
            &:focus {
                outline: none;
                box-shadow: none;
                border: 1px solid rgba(255, 255, 255, 0.25);
            }
        }
    }
    .btn.show {
        color: var(--tc-system-white);
    }
    .form-control {
        color: var(--tc-system-white);
    }
}
.swiper-group-slide-text {
    .swiper-slide {
        width: auto !important;
    }
}
.section-footer-25 {
    .social-icons {
        .icon {
            transition: all 0.3s ease-in-out;
            svg {
                height: 14px;
                width: 14px;
                path {
                    fill: var(--tc-system-black);
                }
            }
            &:hover {
                background-color: var(--tc-theme-primary);
                border-color: var(--tc-theme-primary) !important;
                svg {
                    path {
                        fill: var(--tc-system-white);
                    }
                }
            }
        }
    }
}
// page-about
.yoga-about-section-1 {
    .img-2-sec2 {
        border: 9px solid #fff;
        box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.08);
    }
}
.card-wellness {
    .icon {
        .icon-shape {
            background-color: #f5dfe3;
            z-index: 0;
        }
    }
}
.card-experts {
    .experts-overlay {
        content: '';
        position: absolute;
        transition: all 0.3s linear;
        top: 0;
        left: 0;
        right: 0;
        opacity: 0;
        bottom: 0;
        &::before {
            content: '';
            position: absolute;
            transition: all 0.3s linear;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: var(--tc-theme-primary);
            opacity: 0;
            transform: translateX(-50%);
        }
        .icon {
            position: relative;
            width: 34px;
            height: 34px;
            border-radius: 34px;
            svg {
                height: 14px;
                width: 14px;
                fill: var(--tc-system-white);
                opacity: 0.5;
            }
            &:hover {
                background: var(--tc-system-white);
                svg {
                    fill: var(--tc-theme-primary);
                    opacity: 1;
                    transition-duration: 0.3s;
                }
            }
        }
    }
    &:hover {
        .experts-overlay {
            opacity: 1;
            &::before {
                opacity: 0.9;
                transform: translateX(0%);
            }
        }
    }
}
.form-element-select {
    .form-element {
        input {
            display: none;
        }
        input:checked + label {
            background: var(--tc-theme-primary) !important;
            color: var(--tc-system-white);
        }
    }
}
.form-control {
    height: 60px;
}
.yoga-contact-section-1 {
    .form-group {
        .form-control {
            color: var(--tc-system-black);
            border-radius: 0px;
            border: 1px solid #fff;
            background: #fff;
        }
        textarea {
            min-height: 170px;
        }
        .form-select {
            color: #666;
            background-color: #fff;
            height: 60px;
            border-radius: 0;
            padding-left: 3rem;
            border: 0;
            &:focus {
                outline: none;
                box-shadow: none;
                border: 0;
            }
        }
    }
}
.yoga-about-section-6 {
    .select-form {
        select {
            color: #666;
            background-color: rgba(255, 255, 255, 1);
            height: 60px;
            border-radius: 0;
            padding-left: 3rem;
            border: 0;
            &:focus {
                outline: none;
                box-shadow: none;
                border: 0;
            }
        }
    }
}
.yoga-blog-section-2 {
    .pagination_item {
        background: #ffffff;
        font-weight: 600 !important;
    }
}
.pagination_item {
    background: #f8f2f3;
    font-weight: 600 !important;
}
.btn-secondary {
    --bs-btn-bg: #f5f7f8;
}
.btn-white {
    --bs-btn-hover-color: var(--tc-system-black);
}
.waves {
    position: relative;
    width: 100%;
    height: 15vh;
    margin-bottom: -7px; /*Fix for safari gap*/
    min-height: 100px;
    max-height: 150px;
}
.parallax > use {
    animation: move-forever 25s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite;
}
.parallax > use:nth-child(1) {
    animation-delay: -2s;
    animation-duration: 7s;
}
@keyframes move-forever {
    0% {
        transform: translate3d(-90px, 0, 0);
    }
    100% {
        transform: translate3d(85px, 0, 0);
    }
}
/*Shrinking for mobile*/
@media (max-width: 768px) {
    .waves {
        height: 40px;
        min-height: 40px;
    }
}
